.carousel {
  position: relative;
  width: 100%;
  height: 20rem;

  .carouselItems {
    display: flex;
    overflow: hidden;
    flex-wrap: nowrap;
    gap: 2%;

    .carouselItem {
      height: 20rem;
      transition-duration: 800ms;
      border: 1px solid black;
    }
  }

  .carouselButtonNext,
  .carouselButtonBack {
    position: absolute;
    top: calc(50% - 1.5rem);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3rem;
    height: 3rem;
    cursor: pointer;
    color: var(--purple-color);
    border: 0;
    border-radius: 100%;
    background-color: var(--secondary-color);
  }

  .carouselButtonBack {
    left: 0;
  }

  .carouselButtonNext {
    right: 0;
  }
}

.arrowRight,
.arrowLeft {
  display: inline-block;
  padding: 3px;
  border: solid var(--purple-color);
  border-width: 0 3px 3px 0;
}

.arrowRight {
  transform: rotate(-45deg);
}

.arrowLeft {
  transform: rotate(135deg);
}
