@charset "UTF-8";
/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  line-height: 1.5;
}

/* Удаляем стандартную стилизацию для всех ul и il */
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a {
  text-decoration: none;
}

/* Упрощаем работу с изображениями */
img {
  display: block;
  max-width: 100%;
}

/* Указываем понятную периодичность в потоке данных у article */
article > * + * {
  margin-top: 1em;
}

/* Наследуем шрифты и убираем границы для инпутов и кнопок */
input,
button,
textarea,
select {
  border: none;
  outline: none;
  font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}
#root {
  --white-color: #FFF;
  --black-color: #000;
  --purple-color: #8B32FD;
  --dark-purple-color: #6500CA;
  --silver-color: #F3F3F3;
  --gray-color: #E5E5E5;
  --yellow-color: #FFD600;
  --indent-size-xs: 0.5rem;
  --indent-size-s: 1rem;
  --indent-size-m: 2rem;
  --indent-size-l: 3rem;
  --indent-size-xl: 4rem;
  --font-xs: 0.75rem;
  --font-s: 1rem;
  --font-m: 1.25rem;
  --font-l: 1.5rem;
  --font-xl: 1.75rem;
  --font-xxl: 2rem;
  --round-xs: 0.25rem;
  --round-s: 0.5rem;
  --round-m: 0.75rem;
  --round-l: 1rem;
  --round-xl: 1.5rem;
  --round-xxl: 2rem;
}

#root[data-theme=light] {
  --primary-color: var(--white-color);
  --secondary-color: var(--black-color);
}

#root[data-theme=dark] {
  --primary-color: var(--black-color);
  --secondary-color: var(--white-color);
}

#root {
  height: 100vh;
  font-family: Oxygen, sans-serif;
}